import './App.css'
import {FaTwitter, FaQuoteLeft} from "react-icons/fa";
import { useState } from 'react';

const quoteList = [
  {quote: "Success is the progressive realization of a worthy goal or ideal", author: "Earl Nightingale"},
  {quote: "The trouble with not having a goal is that you can spend your life running up and down the field and never score.", author: "Bill Copeland"},
  {quote: "I think goals should never be easy, they should force you to work, even if they are uncomfortable at the time.", author: "Michael Phelps"},
  {quote: "You should set goals beyond your reach so you always have something to live for.", author: "Ted Turner"},
  {quote: "When it is obvious that the goals cannot be reached, don’t adjust the goals, adjust the action steps.", author: "Confucius"},
  {quote: "There’s nothing better than achieving your goals, whatever they might be.", author: "Paloma Faith", id: 6},
  {quote: "If you set your goals ridiculously high and it’s a failure, you will fail above everyone else’s success.", author: "James Cameron"},
  {quote: "You need lofty goals. Then cement it with a great work ethic.", author: "Jerry West"},
  {quote: "If you want to live a happy life, tie it to a goal, not to people or things.", author: "Albert Einstein"},
  {quote: "One way to keep momentum going is to have constantly greater goals.", author: "Michael Korda"},
  {quote: "I am always more interested in what I am about to do than what I have already done.", author: "Rachel Carson"},
  {quote: "A goal is not always meant to be reached, it often serves simply as something to aim at.", author: "Bruce Lee"},
  {quote: "If something is important enough, even if the odds are against you, you should still do it.", author: "Elon Musk"},
  {quote: "It always seems impossible until it’s done.", author: "Nelson Mandela"},
  {quote: "Success consists of going from failure to failure without loss of enthusiasm.", author: "Winston Churchill"},
  {quote: "Things won are done; joy’s soul lies in the doing.", author: "William Shakespeare"},
  {quote: "I love the challenge of starting at zero every day and seeing how much I can accomplish.", author: "Martha Stewart"},
  {quote: "All our dreams can come true, if we have the courage to pursue them.", author: "Walt Disney"},
  {quote: "The goal is not to be better than the other man, but your previous self.", author: "Dalai Lama"},
  {quote: "To win big you sometimes have to risk big things.", author: "Bill Gates"}
]

const color = [
  '#16a085',
  '#27ae60',
  '#2c3e50',
  '#f39c12',
  '#e74c3c',
  '#9b59b6',
  '#FB6964',
  '#342224',
  '#472E32',
  '#BDBB99',
  '#77B1A9',
  '#73A857'
]



function App() {

  const randomQuote = [...quoteList].sort(() => Math.random() - 0.5);
  const randomColor = [...color].sort(() => Math.random() - 0.5);

   const [quot, setQuot] = useState(randomQuote[0])
   const [col, setCol] = useState(randomColor[0])

  const hanldeButton = () => {
    setQuot(randomQuote[10])
    setCol(randomColor[3])
  }

  return (
    <div  style={{ display: "flex", width: "100vw", height: "100vh", justifyContent: "center", alignItems: "center", backgroundColor: col}}>
      <div id="quote-box" >
        <div className="text-and-icon">
          <FaQuoteLeft className="quote-icon" style={{color: col}}/>
          <h1 id="text" style={{color: col}}>{quot.quote}</h1>
        </div>
        <p id="author" style={{color: col}}>-{quot.author}</p>
        <div className="button-wraper"> 
          <button id="new-quote" onClick={hanldeButton} style={{backgroundColor: col}}>New quote</button>
          <div className="tweet-button">
            <a style={{backgroundColor: col}} id="tweet-quote" href={encodeURI(`http://www.twitter.com/intent/tweet?text="${quot.quote}" -${quot.author}`)}>
            <FaTwitter style={{backgroundColor: col, color: "white"}}/></a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
